import QrCodeCreate from "./QrCodeCreate";
import QrCodeDecode from "./QrCodeDecode";

import { createHashRouter } from "react-router-dom";

export const router = createHashRouter([
  {
    path: "/",
    element: <QrCodeCreate />,
  },
  {
    path: "/decode",
    element: <QrCodeDecode />,
  },
  {
    path: "/*",
    element: <QrCodeCreate />,
  }
]);
